ction-mini-card\" v-for=\"qcollection in queryQcollection.results\" @click=\"newQuickquiz.qcollection = qcollection\"> <span class=name>{{qcollection.name}}</span> <div class=flex-row style=\"align-items: baseline\"> <span class=subject>{{qcollection.subject | subject}}</span> <span class=subject style=background:#0277BD>{{qcollection.questions}} 題</span> <span class=difficulty>难度 {{qcollection.aveDifficulty}}</span> </div> </div> </div> </div> </div> <div class=flex-column v-show=qrcode> <canvas id=qrcode-canvas class=qrcode></canvas> <p style=text-align:center>掃描二維碼進行答題</p> </div> <mdl-button primary raised @click=startQuickQuiz() :disabled=\"!formCompletion && !submitButton\">開始小測并生成 QR Code</mdl-button> </div> </div> </div> ";


